import { configureStore, createSlice } from '@reduxjs/toolkit'



let user = createSlice({

   name: 'user',

   initialState: { name: '박수빈' },

   reducers: {



      changeName(state) {

         state.name = 'john kim'

      },



      increase(state, action) {

         state.age += action.payload


      }

   }

})
let cart = createSlice({

   name: 'cart',

   initialState: [



   ],

   reducers: {

      addCount(state, action) {

         let idx = action.payload;


         ++state[idx].amount;

      },
      decreaseCount(state, action) {
         let idx = action.payload;
         if (state[idx].amount > 1) {
            state[idx].amount--;
         } else {
            if (window.confirm('장바구니에서 삭제하시겠습니까?')) { state.splice(idx, 1); }

         }
      },


      sortName(state) {

         state.sort((a, b) =>

            a.item > b.item ? 1 : -1,

         );

      },

      addItem(state, action) {
         let num = state.findIndex(a => a.id === action.payload.id)
         if (num >= 0) {
            state[num].amount++;
         } else {
            state.push(action.payload);
         }
      },

   }

})



export let { addCount, sortName, addItem, decreaseCount } = cart.actions
export let { changeName, increase } = user.actions
export default configureStore({

   reducer: {

      user: user.reducer,

      cart: cart.reducer

   }

}) 
