import React from 'react';

import { Button, Container, Table } from 'react-bootstrap';

import { useDispatch, useSelector } from "react-redux";

import { addCount, sortName,decreaseCount } from "./../store.js"; 
import { Row, Col } from 'react-bootstrap';



function Cart(){

   let user = useSelector(state => state.user);

   let cart = useSelector(state => state.cart);

   let { name, age } = user;

   // console.log(state.cart[0].name);

   // dispatch는 store.js 로 요청보내주는 함수

   let dispatch = useDispatch() //4. useDispatch() import하고 사용

   return(

      <Container style={{ paddingTop: '100px', paddingBottom:'10%' }} className='Cart'>

         <h5>{name} {age}(님)의 장바구니</h5> 

    {/* <button onClick={()=>{

          dispatch(increase(1))}

       }>나이+</button> //5. dispatch(state변경함수()) 사용

       <button onClick={()=>{

          dispatch(increase(100))}}>나이+</button> */}



         <Table>

            <thead>

               <tr>

                  <th className='Cartremove'>상품</th>

                  <th className='Cartremove'>수량</th>

                  <th className='Cartremove'> 낱개 가격</th>
                    <th className='Cartremove1'> 총가격</th>

                  <th className='Cartremove'>변경하기</th>

               </tr>

            </thead>

            <tbody>

            {

               cart.map((v, i) =>

                  <tr key={i}>

                   

                     <td >
                        <Row >
                           <Col md={6}>
                        <img src={process.env.PUBLIC_URL + v.imgUrl} alt={v.item} style={{ width: '100px', marginRight: '10px' }} /></Col>
                          <Col md={6}>{v.item}</Col>
                        </Row>
                     </td>

                     <td  >{v.amount +'개'}</td>
                       <td>{v.price}</td>
                     <td className='Cartremove1'>{v.price*v.amount}</td>

                     <td className='amoutBTN'>
                        <button onClick={()=>

                        dispatch(addCount(i))

                     }><span class="material-symbols-outlined">
add
</span></button>
                       <button onClick={()=>

                        dispatch(decreaseCount(i))

                     }><span class="material-symbols-outlined">
remove
</span></button></td>

                  </tr>

               )

            }

            </tbody>

         </Table>

         <button  onClick={()=>{

            dispatch(sortName())

         }}>결제하러가기</button>{' '}

      </Container>

   )

}



export default Cart
