import React, { useRef, useState } from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import Data from '../Data.js';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';



// import required modules
import { Pagination } from 'swiper/modules';
import { Container } from 'react-bootstrap';

export default function App() {
console.log(Data[0])

   const [best] = useState(Data[1]);
  return (
    <Container className="mySwiper3">
      <h3>구매자후기</h3>
      <p>각자의 레시피가 담긴 박쉐프한상</p>
      <Swiper slidesPerView={'2'}
        spaceBetween={5}
        pagination={{
          clickable: true,
        }}
   
        breakpoints={{
          640: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          768: {
            slidesPerView: 3,
            spaceBetween: 40,
          },
          1024: {
            slidesPerView: 4,
            spaceBetween: 30,
          },
        }} modules={[Pagination]} className="mySwiper3">
         {best.map((product,index) =>(<SwiperSlide key={index}>
    
          <div className="imgbox">
            <img src={process.env.PUBLIC_URL +product.imageSrc}  alt={product.altText} />
          </div>
          <div className="txtbox">
           <p>{product.content}</p>
           
          </div> 
          <p className='ReIn'>
            <span>{product.reviewid}</span>| &nbsp;
            <span>{product.date}</span>
            </p>
          </SwiperSlide>))}

        
      </Swiper>
      <button>리뷰쓰기</button>
    </Container>
  );
}
