import React from 'react'
import { Container, } from 'react-bootstrap'

const Banner = () => {
  return (

    <div className='banner1' style={{ backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback5.png)` }}>


      <Container sm={6}>
        <div className="txtbox">
          <h5>3+1 프로모션</h5>
          <h4>불고기의 끝을 경험하라<br />도전해볼텐가?</h4>
          <h3>세상에서 제일 매운 불고기</h3>
          <button onClick={() => alert('장바구니에 담았습니다!')}>
            장바구니 담기
          </button>
        </div>
      </Container>
    </div>
  )
}

export default Banner