import React, { useRef, useState } from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/effect-fade';
import 'swiper/css/navigation';
import 'swiper/css/pagination';



// import required modules
import { EffectFade, Navigation, Pagination } from 'swiper/modules';
import { Container } from 'react-bootstrap';

export default function App() {
  return (
    <>
      <Swiper
        spaceBetween={30}
        effect={'fade'}
        navigation={true}
        pagination={{
          clickable: true,
        }}
        modules={[EffectFade, Navigation, Pagination]}
        className="mySwiper mainslider"
      >
        <SwiperSlide   style={{
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback1.png)`,
    backgroundColor:  "white"

  }}>       
        
           <Container>
            <div className="txtbox">
            <h3  >집에서 즐기는 고급정찬</h3>
            <p>박쉐프가 건강하고 정성스럽게 준비</p>
            <button> 사러가기</button>
            </div>
          </Container>
       </SwiperSlide>
       
     <SwiperSlide   style={{
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback2.png)`,
    backgroundColor:  "white"

  }}>       
        
           <Container>
            <div className="txtbox">
            <h3  style={{ color:'white'}}>세상에서 가장 매운 소불고기</h3>
            <p style={{ color:'white'}}>맛있게맵다</p>
            <button style={{ backgroundColor:'#ea462b'}}> 사러가기</button>
            </div>
          </Container>
       </SwiperSlide>
         <SwiperSlide   style={{
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback3.png)`,
    backgroundColor:  "white"

  }}>       
        
           <Container>
            <div className="txtbox">
            <h3>집에서 즐기는 고급정찬</h3>
            <p>박쉐프가 건강하고 정성스럽게 준비</p>
            <button> 사러가기</button>
            </div>
          </Container>
       </SwiperSlide>
        <SwiperSlide style={{backgroundImage:`url(${process.env.PUBLIC_URL}/img/miansliderback4.png)`}}>       
        
           <Container>
            <div className="txtbox">
            <h3>소규모 샵인샵 창업</h3>
            <p>가맹비,교육비,로얄티무료</p>
            <button> 문의하기</button>
            </div>
          </Container>
       </SwiperSlide>
      </Swiper>
    </>
  );
}
