import React from 'react'
import { Container, Table } from 'react-bootstrap'

const ShippingPolicy = () => {
   return (
      <Container className='ShippingPolicy'>
         <h3>배송정보</h3>
         <Table striped="columns">
     <tbody>
    <tr>
      <th>배송방법</th>
      <th>신선/냉장/냉동</th>
      <th rowSpan={2}>배송비</th>
      <th rowSpan={2}>무료배송<br />도서산간 지역 배송불가</th>
    </tr>

    <tr>
      <td>배송사</td>
      <td>롯데택배</td>
    </tr>
  </tbody>
         </Table>
         <h3>교환/반품안내</h3>
         <p>ㆍ교환/반품에 관한 일반적인 사항은 판매자가 제시사항보다 관계법령이 우선합니다.
            다만,판매자의 제시사항이 관계법령보다 소비자에게 유리한 경우에는 판매자 제시사항이 적용됩니다.</p>
         <Table striped="columns">
         
  <tbody>
    <tr>
      <th style={{ width: '20%', verticalAlign: 'top', textAlign: 'left' }}>교환/반품 비용<br />(왕복비용)</th>
      <td>
        4,000원<br />
        - 단, 고객 변심의 경우에만 발생<br />
        - 도서산간 지역 배송불가
      </td>
    </tr>
    <tr>
      <th style={{ verticalAlign: 'top', textAlign: 'left' }}>교환/반품 신청 기준일</th>
      <td>
        - 단순변심에 의한 교환/반품은 제품 수령 후 7일 이내까지, 교환/반품 제한사항에 해당하지 않는 경우에만 가능<br />
        (배송비용과 교환/반품 비용 왕복배송비 고객부담)<br />
        - 상품의 내용이 표시/광고의 내용과 다른 경우에는 상품을 수령한 날부터 3개월 이내,<br />
        그 사실을 안 날 또는 알 수 있었던 날부터 30일 이내에 청약철회 가능
      </td>
    </tr>
  </tbody>
         </Table>
         <div>
            <ul>교환/반품 제한사항
               <li>ㆍ주문/제작 상품의 경우, 상품의 제작이 이미 진행된 경우</li>
               <li>ㆍ상품 포장을 개봉하여 사용 또는 설치 완료되어 상품의 가치가 훼손된 경우 (단, 내용 확인을 위한 포장 개봉의 경우는 예외)</li>
               <li>ㆍ고객의 사용, 시간경과, 일부 소비에 의하여 상품의 가치가 현저히 감소한 경우</li>
               <li>ㆍ세트상품 일부 사용, 구성품을 분실하였거나 취급 부주의로 인한 파손/고장/오염으로 재판매 불가한 경우</li>
               <li>ㆍ모니터 해상도의 차이로 인해 색상이나 이미지가 실제와 달라, 고객이 단순 변심으로 교환/반품을 무료로 요청하는 경우</li>
               <li>ㆍ제조사의 사정 (신모델 출시 등) 및 부품 가격 변동 등에 의해 무료 교환/반품으로 요청하는 경우</li>

            </ul>
         </div>
 

      </Container>
   )
}

export default ShippingPolicy