import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'

const Banner2 = () => {
  return (
    <div className='Banner2' style={{
      backgroundImage: `url(${process.env.PUBLIC_URL}/img/change.jpg)`
    }} >
      <Container className='b2C'>
        <Row>
          <Col className="imgbox" md={6}>
            <img src={process.env.PUBLIC_URL + '/img/haccupfile.png'} alt="해썹문서" />
          </Col>
          <Col className="txtbox"md={6}>
           <img src={process.env.PUBLIC_URL + '/img/haccuppop.png'} alt="해썹문구" /></Col>
        </Row>
      </Container>
    </div>
  )
}

export default Banner2